.movieRow {
    margin-bottom: 30px;
}

.movieRow h2 {
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea {
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list {
    transition: all ease 0.7s;
}

.movieRow--item {
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img {
    width: 100%;
    transform: scale(0.9);
    transition: all ease 0.3s;
}

.movieRow--item img:hover {
    transform: scale(1);
}

.movieRow--Left,
.movieRow--Right {
    position: absolute;
    width: 40px;
    height: 225px;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 99;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    opacity: 0;
    transition: all ease 0.5s;
} 
.movieRow--Left {
    left: 0;
}
.movieRow--Right {
    right: 0;
}
.movieRow:hover .movieRow--Left,
.movieRow:hover .movieRow--Right {
    opacity: 1;
}

@media (max-width: 760px) {
    .movieRow--Right,
    .movieRow--Left {
        opacity: 1;
        transform: scale(0.9);
    }


}