Header {
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    right: 0;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 30px 0px;
    background: transparent;
    transition: all ease 0.4s;
}

header.Black {
    background-color: #141414; 
}

.Header--logo {
    height: 33px;


}
.Header--logo img {
    height: 100%;
}
.Header--User {
    height: 47px;

}
.Header--User img {
    height: 100%;
    border-radius: 3px;
}